html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  min-width: 320px; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  pointer-events: none;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  text-indent: -9999px;
  white-space: nowrap;
  overflow: hidden; }

.header {
  position: relative;
  padding-top: 47px;
  padding-bottom: 146px;
  background-image: url("../img/girl.png"), url("../img/israel.png"), url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-position: 50% 50%, 50% 28%, 50% 0;
  text-align: center; }

.header__menu {
